.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  margin-top: 0px;
  margin-left: 0px;
  width: 100%;
  height: 100%;
  position: absolute;
  font-family: Calibri;
}

.heading {
  border-bottom: 1px solid;
  padding-left: 9px;
  margin-bottom: 5px;
}

.font-head {
  font-size: 22px;
  font-weight: bold;
}

.font-small {
  font-size: 16px;
  font-weight: bold;
}

#root {
  margin: 40px auto;
  width: 700px;
  -webkit-box-shadow: 0px 0px 111px -14px rgba(0,0,0,0.55);
  -moz-box-shadow: 0px 0px 111px -14px rgba(0,0,0,0.55);
  box-shadow: 0px 0px 111px -14px rgba(0,0,0,0.55);
  padding: 40px;
}

